.push-list {
    width: 100%;
    padding: 23px 30px 27px 30px;
    border: 5px solid #675E5E;
    font-size:26px;
}
.manage-btn {
    margin-top: 12px;
    display: flex;
    justify-content: center;
}
.btn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 180px;
    height: 74px;
    background: #C4C4C4;
    margin-right: 20px;
    border-radius: 25px;
    color:#fff;
    font-size: 26px;
    border-color: aliceblue;
    outline: none;
}