* {
    padding: 0;
    margin:0;
    box-sizing: border-box;
}
body {
    font-weight: 100;
    font-size: 26px;
    font-family: 'Roboto', sans-serif;
    font-style: normal;
}
a {
    text-decoration: none;
}
.wrapper {
    background-color:#AB9DFC;
    height: 100vh;
    display: flex;
    align-items: center;
}
.container {
    width: 100%;
    max-width: 567px;
    margin:0 auto;
}